.settings-dialog {
  border: none;
  border-radius: 8px;
  padding: 0;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  color: #000;
}

.darkMode .settings-dialog {
  background-color: #222;
  color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
}

.settings-dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}

.darkMode .settings-dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
}

.settings-content {
  padding: 24px;
  position: relative;
}

.settings-content h2 {
  margin: 0 0 24px 0;
  font-size: clamp(1.3rem, 4vw, 1.5rem);
  text-align: center;
  font-weight: 500;
  padding-right: 48px;
  padding-left: 48px;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  background: none;
  border: 1.5px solid #000;
  border-radius: 50%;
  font-size: 1.5rem;
  font-weight: 200;
  cursor: pointer;
  color: inherit;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.close-button svg {
  width: 1em;
  height: 1em;
  stroke-width: 1;
}

.darkMode .close-button {
  border-color: #fff;
}

.close-button:hover,
.close-button:focus {
  background-color: rgba(0, 0, 0, 0.1);
  outline-offset: 3px;
}

.darkMode .close-button:hover,
.darkMode .close-button:focus {
  background-color: rgba(255, 255, 255, 0.1);
}

.settings-options {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.setting-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.setting-item >500el,
.setting-item > p {
  font-weight: bold;
  font-size: 1.1rem;
  margin: 0;
}
